#productImage{
    padding-left: 15px;
    padding-bottom: 15px;
}
.sizeButton{
   height: 45px;
   width: 45px;
   font-size: 13px;
   font-weight: bold;
   background-color: white;
   color: rgb(105, 100, 100);
   border: 1px solid rgb(201, 196, 196);
   border-radius: 50%;
}
.sizeCross{
    /* position: relative; */
   height: 45px;
   width: 45px;
   font-size: 13px;
   font-weight: bold;
   background-color: whitesmoke;
   color: rgb(105, 100, 100);
   border: 1px solid rgb(238, 232, 232);
   border-radius: 50%;

}
.sizeCross:focus{
 outline: none;
}
.sizeCross:hover{
    /* position: relative; */
  cursor: not-allowed;
}
.crossLine1{
    position: absolute;
    top: 22px; 
    left: 8px;
    width: 45px;
    height: 1px;
    background-color: rgb(155, 143, 143);
    align-items: center;
    transform: rotate(-45deg);

}

.sizeButton:hover{
    border: 1px solid orange;
 }
 .sizeButton:focus{
    outline: none;
    border: 1px solid orange;
 }
 .cart{
     padding: 10px;
     border: none;
     width: 210px;
     background-color: orange;
     color: white;
     border-radius: 5px;
     font-weight: bold;
 }
 .cart:hover{
    background-color: rgb(238, 155, 2);
}
 .cart:focus{
    outline: none;
}
.select{
    margin-left: 10px;
    padding-left: 10px;
    padding-right: 10px;
    border: 1px solid rgb(185, 181, 181);
    border-radius: 5px;
}
.select:focus{
   outline: none;
}
.spectTitle{
    font-size: 12px;
    color: grey;
    margin-top: 5px;
}
.spectValue{
    margin-bottom: 5px;
}
.stock{
    font-size: 13px;
    padding-left: 15px;
    padding-top: 5px;
    color: orange;
}

